'use strict';


function medicine(t) {
  return t.medicine;
}

function doasge(t) {
  return t.doasge;
}

function days(t) {
  return t.days;
}

function empty(param) {
  return {
          medicine: "",
          doasge: "",
          days: 0
        };
}

function updateMedicine(medicine, t) {
  return {
          medicine: medicine,
          doasge: t.doasge,
          days: t.days
        };
}

exports.medicine = medicine;
exports.doasge = doasge;
exports.days = days;
exports.empty = empty;
exports.updateMedicine = updateMedicine;
/* No side effect */
